.masthead_container {
  display: flex;
  flex-direction: column;
  .masthead_subheading {
    font-family: "Lora";
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 46px;
    color: #FFFFFF;
    margin: 0;
    padding-top: 38px; 
  }
  .masthead_paragraph {
    font-family: "Montserrat_thin";
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    max-width: 410px;
    padding-bottom: 25px; 
  }
  .masthead_info {
    display: inline;
    margin-bottom: 26px;
    font-family: "Montserrat_bold";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    text-decoration: none;
  }
  .masthead_info svg {
    margin-bottom: -9px;
  }
  .masthead_info:hover {
    color: blue;
  }
  .masthead_info:hover svg {
    fill: blue;
  }
}
@media (min-width: 720px) {
  .masthead_container {
    display: block;
    .masthead_subheading {
      font-size: 48px;
      line-height: 61px;
      padding-top: 0;
      padding-bottom: 40px; 
    }
    .masthead_paragraph {
      max-width: 355px;
      font-weight: 300;
      font-size: 24px;
      line-height: 29px;
      padding-bottom: 48px; 
    } 
  }
}